@mixin sm
{
    @media (min-width: 34em)
    {
        @content;
    }
}

@mixin md
{
    @media (min-width: 48em)
    {
        @content;
    }
}

@mixin lg
{
    @media(min-width: 62em)
    {
        @content;
    }
}

@mixin xl
{
    @media (min-width: 75em)
    {
        @content;
    }
}

@mixin aside
{
    @media (min-width: 1303px)
    {
        @content;
    }
}
